'use client';
import React, { useState } from 'react';
import AllOrders from '@/components/orders/all-orders';
import PendingOrders from '@/components/orders/pending';
import CompletedOrders from '@/components/orders/completed-orders';
import OrderHistory from '@/components/orders/order-hstory';
import { FaList, FaClock, FaCheck, FaHistory } from 'react-icons/fa'; // Added icons

const tabs = [
  { id: 'all', label: 'All Orders', icon: <FaList /> },
  { id: 'pending', label: 'Pending Orders', icon: <FaClock /> },
  { id: 'completed', label: 'Completed Orders', icon: <FaCheck /> },
  { id: 'history', label: 'Order History', icon: <FaHistory /> },
];

export default function OrderDashboard() {
  const [activeTab, setActiveTab] = useState('all');

  const renderTabContent = () => {
    switch (activeTab) {
      case 'pending':
        return <PendingOrders />;
      case 'completed':
        return <CompletedOrders />;
      case 'history':
        return <OrderHistory />;
      default:
        return <AllOrders />;
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="bg-white shadow mb-6">
        <div className="container mx-auto px-6 py-4">
          <h1 className="text-4xl font-bold text-center text-gray-700">Order Dashboard</h1>
        </div>
      </div>

      <div className="container mx-auto px-6">
        {/* Tab Navigation */}
        <div className="flex flex-wrap justify-center space-x-6 mb-8 gap-4">
          {tabs.map((tab) => (
            <button
              key={tab.id}
              className={`flex items-center space-x-2 px-6 py-3 text-sm font-medium rounded-lg transition-all duration-300 ${
                activeTab === tab.id
                  ? 'bg-blue-600 text-white'
                  : 'bg-gray-200 text-gray-700 hover:bg-blue-500 hover:text-white'
              }`}
              onClick={() => setActiveTab(tab.id)}
            >
              {tab.icon}
              <span className="hidden sm:inline">{tab.label}</span>
            </button>
          ))}
        </div>

        {/* Tab Content */}
        <div className="bg-white rounded-lg shadow-lg p-6 sm:p-8 transition-all duration-300">
          {renderTabContent()}
        </div>
      </div>
    </div>
  );
}
